.mini-dashboard {
    width: 100%;
    max-width: 1172px;
    min-height: 370px;
    background-color: #27293c;
    box-shadow: 0 8px 16px -4px rgba(0, 0, 0, 0.5);
    border: 1px solid #333548;
    border-radius: 2px;
    margin: 0 auto;
    @media screen and (max-width: 1170px) {
        width: 95%;
    }
}